.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background: -webkit-gradient(linear, left top, left bottom, from(#6300c8), to(#36d3fe)) #9000ff;
  background: -webkit-linear-gradient(top, #6300c8 0%, #36d3fe 100%) #9000ff;
  background: -o-linear-gradient(top, #6300c8 0%, #36d3fe 100%) #9000ff;
  background: linear-gradient(to bottom, #6300c8 0%, #36d3fe 100%) #9000ff;
  height: 60vh;
  padding: 20px;
  color: white;
  position: relative;
}

.App-title {
  font-size: 1.5em;
  position: absolute;
  bottom: 40px;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.demo-wrap {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.demo-content {
  width: 100vw;
}

.demo-title {
  font-size: 2rem;
  color: #002e63
}

.warning {
  font-size: 20px;
  color: orange;
  margin: 20px auto;
  width: 60%;
}

.warning .red {
  color: red;
  font-weight: 600;
}



