.flex-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.bg-demo3 {
  background-image: -webkit-gradient(linear, left top, right bottom, from(#74ebd5), to(#acb6e5));
  background-image: -webkit-linear-gradient(top left, #74ebd5, #acb6e5);
  background-image: -o-linear-gradient(top left, #74ebd5, #acb6e5);
  background-image: linear-gradient(to bottom right, #74ebd5, #acb6e5);
}

.placeholder-jagged-props {
  background-image: paint(jagged-edge);
  width: 300px;
  height: 150px;
  margin-top: 60px;
}

.placeholder-jagged-props:nth-child(1) {
  --tooth-width: 50px;
  --tooth-height: 25px;
}
.placeholder-jagged-props:nth-child(2) {
  --tooth-width: 2rem;
  --tooth-height: 3rem;
}
.placeholder-jagged-props:nth-child(3) {
  --tooth-width: calc(33vw - 31px);
  --tooth-height: 2em;
}



